<template>
  <Input
    v-model="value"
    hide-shadow
    bordered
    max-length="8"
    v-bind="$attrs"
    class="code-input"
  />
</template>

<script>
import { useModel } from '@/shared/utils/useModel'
import Input from '@/shared/components/UIKit/Input'

export default {
  name: 'CodeInput',
  inheritAttrs: true,
  components: { Input },
  props: {
    modelValue: {
      type: [Number, String],
      required: true
    }
  },
  emits: ['update:modelValue'],
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    return {
      value
    }
  }
}
</script>

<style lang="scss">
.code-input {
  padding: 20px 48px;

  .input__body__field-input {
    font-size: 32px;
    line-height: 37px;
    letter-spacing: 0.3em;
    text-align: center;
  }
  .input__body__field-placeholder {
    @include position-align-center;

    width: fit-content;
    font-size: 18px;
    line-height: 21px;
  }
}
</style>
