<template>
  <div class="admin-login-step-2">
    <AuthTitle>
      {{ tp('twoFactor') }}
    </AuthTitle>
    <Form class="d-flex flex-column align-items-center">
      <CodeInput
        v-model="form.code.value"
        :error="form.code.error"
        :placeholder="tp('enterHere')"
      />
      <Text
        v-if="error"
        mt="8px"
        class="admin-login-step-2__error"
      >{{ tp('invalidCode') }}</Text>
      <div class="admin-login-step-2__actions">
        <Button type="primary" @click="onSubmit">
          <Text
            weight="400"
            size="14px"
            line-height="16px"
            color="white"
          >{{ tp('verifyBtn') }}</Text>
        </Button>
        <Button @click="onBack">
          <Text
            weight="400"
            size="14px"
            line-height="16px"
            color="black"
          >{{ tp('backBtn') }}</Text>
        </Button>
      </div>
    </Form>
  </div>
</template>

<script>
import { ROUTES_NAMES } from '@/admin/common/routes'
import { useI18n } from '@/shared/plugins/i18n'
import { reactive, ref } from 'vue'
import { useGoBack, useRouter } from '@/shared/plugins/vue-router'
import Form from '@/shared/components/Form/Form'
import Button from '@/shared/components/UIKit/Button'
import AuthTitle from '@/admin/views/Auth/AuthTitle'
import CodeInput from '@/admin/components/Inputs/CodeInput'
import Text from '@/shared/components/UIKit/Text/Text'

export default {
  name: 'Step2',
  components: {
    Text,
    Button,
    Form,
    CodeInput,
    AuthTitle
  },
  emits: ['submit'],
  setup (_, { emit }) {
    const { tp } = useI18n('adminAuth.login')
    const goBack = useGoBack({
      name: ROUTES_NAMES.DASHBOARD
    })
    const router = useRouter()

    const error = ref(false)
    const form = reactive({
      code: {
        value: '',
        error: false,
        validate: () => {
          form.code.error = form.code.value.length !== 8
          return !form.code.error
        }
      }
    })

    const onSubmit = () => {
      debugger
      error.value = validate()
      if (error.value) {
        return
      }
      router.push({
        name: ROUTES_NAMES.DASHBOARD
      })
      emit('submit', form)
    }
    const onBack = () => {
      goBack()
    }
    const validate = () => {
      form.code.validate()

      return form.code.error
    }

    return {
      tp,
      form,
      error,
      onBack,
      onSubmit
    }
  }
}
</script>

