  padding: 40px 55px 0 55px;

  .admin-auth-title {
    margin-bottom: 30px;
  }

  &__actions {
    display: flex;
    width: 100%;
    margin-top: 30px;

    .button {
      padding: 14px 25px;

      &:first-of-type {
        flex-grow: 1;
        margin-right: 20px;
      }
    }
  }
  &__error {
    color: $adminErrorColor;
  }
}
</style>
